
.card-content.inverted, .has-gradient-border-hover {
  --border-width: 3px;
  position: relative;
  display: flex;
  //justify-content: center;
  //align-items: center;
  //width: 300px;
  //height: 200px;
  //font-size: 2.5rem;
  //text-transform: uppercase;
  color: white;
  background: #222;
  border-radius: var(--border-width);


  z-index: 1;
  //z-index: 2;

}



.card-content.inverted:hover, .has-gradient-border-hover:hover, .gradient-border {
  --border-width: 3px;
  position: relative;
  display: flex;
  //justify-content: center;
  //align-items: center;
  //width: 300px;
  //height: 200px;
  //font-size: 2.5rem;
  //text-transform: uppercase;

  color: white;
  background: #222;
  border-radius: var(--border-width);

  &::after {



    z-index: -1;


    position: absolute;
    content: "";
    top: calc(-1 * var(--border-width));
    left: calc(-1 * var(--border-width));
    width: calc(100% + var(--border-width) * 2);
    height: calc(100% + var(--border-width) * 2);
    background: linear-gradient(
                    60deg,
                    hsl(224, 85%, 66%),
                    hsl(269, 85%, 66%),
                    hsl(314, 85%, 66%),
                    hsl(359, 85%, 66%),
                    hsl(44, 85%, 66%),
                    hsl(89, 85%, 66%),
                    hsl(134, 85%, 66%),
                    hsl(179, 85%, 66%)
    );
    background-size: 300% 300%;
    background-position: 0 50%;
    border-radius: calc(2 * var(--border-width));
    animation: moveGradient 4s alternate infinite;
  }
}

@keyframes moveGradient {
  50% {
    background-position: 100% 50%;
  }
}
